<template>
  <tas-base-crud :key="mainKey" :config="config" hide-create hide-update hide-detail>
    <template v-slot:list-action>
      <a v-if="$_sys.isAllowed(config.permission.create)" v-b-modal.modal-create-simpan-pinjam class="btn btn-primary btn-icon btn-circle ml-3">
        <i class="ri-add-line p-0"></i>
      </a>
      <b-modal id="modal-create-simpan-pinjam" hide-footer hide-header centered size="md" :title="config.title">
        <div class="">
          <h5 class="font-wight-bolder">Informasi</h5>
          <p> Sebelum membuat <b>{{ config.title }}</b>, pastikan konfigurasi modul di bawah ini sudah sesuai dengan periode yang dipilih :</p>
          <div class="form-group mb-2">
            <span>Periode Iuran <span class="text-danger">*</span></span>
            <s-input @input="checkThisMonth()" v-model="firstForm.periode_month" :item="{ type:'month', validation:[], placeholder: 'Pilih Periode Iuran Simpan / Pinjam', api:'', formatter:momentFormat, ic:'w-100 mt-0' }" />
          </div>
          <ul class="pl-0 mt-0 mb-5 list-unstyled">
            <li class="d-flex align-items-center pointer arrow-info-inside" @click="redirect('periode-penggajian')">
              <i v-if="isAvailablePeriod" class="ri-checkbox-line text-success mr-2"></i>
              <i v-else class="ri-checkbox-blank-line text-danger mr-2"></i> Periode Penggajian <b-spinner v-if="isLoadingPeriod" small class="ml-2"></b-spinner>
              <i v-else class="ri-arrow-right-line icon-arrow"></i>
            </li>
            <li v-if="(!isLoadingPeriod && firstForm.periode_month && !isAvailablePeriod)" class="font-size-sm text-danger">Periode Penggajian {{ firstForm.periode_month | parse('period') }} belum dibuat !</li>
            <li class="d-flex align-items-center pointer arrow-info-inside" @click="redirect('mapping-upah-BPJS')"> <i class="ri-checkbox-line text-success mr-2"></i> Mapping Upah BPJS <i class="ri-arrow-right-line icon-arrow"></i></li>
            <li class="d-flex align-items-center pointer arrow-info-inside" @click="redirect('setup-gaji-tenaga-kerja')"> <i class="ri-checkbox-line text-success mr-2"></i> Setup Gaji Tenaga Kerja <i class="ri-arrow-right-line icon-arrow"></i></li>
            <li class="d-flex align-items-center pointer arrow-info-inside" @click="redirect('premi-BPJS-kesehatan')">
              <i v-if="isAvailablePremi" class="ri-checkbox-line text-success mr-2"></i>
              <i v-else class="ri-checkbox-blank-line text-danger mr-2"></i> Premi BPJS Kesehatan <b-spinner v-if="isLoadingPremi" small class="ml-2"></b-spinner>
              <i v-else class="ri-arrow-right-line icon-arrow"></i>
            </li>
            <li v-if="(!isLoadingPremi && firstForm.periode_month && !isAvailablePremi)" class="font-size-sm text-danger">Premi pada periode {{ firstForm.periode_month | parse('period') }} belum dibuat !</li>
          </ul>
        </div>
        <div class="text-right">
          <button :disabled="!(firstForm.periode_month && isAvailablePremi && isAvailablePeriod)" @click="generateMonth()" class="btn btn-primary"> Buat {{ config.title }} </button>
        </div>
      </b-modal>
    </template>
    <template v-slot:list-table-action="data">
      <a @click="$children[0].getEvent('detail', data.rowData)" class="btn btn-info btn-sm"> Lihat Detail Iuran </a>
    </template>
    <template v-slot:header-table>
      <div class="card-body py-0">
        <div v-if="isAlertShow" class="alert alert-custom alert-light-danger p-2 mb-0" role="alert">
          <div class="alert-icon">
            <span class="svg-icon svg-icon-danger svg-icon-xl">
              <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                <g stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
                  <rect x="0" y="0" width="24" height="24" />
                  <path d="M11.1669899,4.49941818 L2.82535718,19.5143571 C2.557144,19.9971408 2.7310878,20.6059441 3.21387153,20.8741573 C3.36242953,20.9566895 3.52957021,21 3.69951446,21 L21.2169432,21 C21.7692279,21 22.2169432,20.5522847 22.2169432,20 C22.2169432,19.8159952 22.1661743,19.6355579 22.070225,19.47855 L12.894429,4.4636111 C12.6064401,3.99235656 11.9909517,3.84379039 11.5196972,4.13177928 C11.3723594,4.22181902 11.2508468,4.34847583 11.1669899,4.49941818 Z" fill="#000000" opacity="0.3" />
                  <rect fill="#000000" x="11" y="9" width="2" height="7" rx="1" />
                  <rect fill="#000000" x="11" y="17" width="2" height="2" rx="1" />
                </g>
              </svg>
            </span>
          </div>
          <div class="alert-text">Data {{ config.title }} pada bulan ini belum dibuat!</div>
        </div>
      </div>
    </template>
  </tas-base-crud>
</template>
<style scoped>
  .arrow-info-inside:hover:not(i) {
    font-weight: bold;
    color: var(--primary);
  }
  .arrow-info-inside .icon-arrow {
    transition: all 0.5s ease;
    padding-left: 0.25rem;
  }
  .arrow-info-inside:hover .icon-arrow {
    padding-left: 0.8rem;
    color: var(--primary);
  }
</style>

<script>
import moment from 'moment'
moment.locale('id')
export default {
  name: 'crud-bpjs-kes-bills',
  data () {
    return {
      mainKey: 1,
      isAlertShow: false,
      isLoadingPremi: false,
      isAvailablePremi: false,
      isAvailablePeriod: false,
      isLoadingPeriod: false,
      firstForm: {
        periode_month: null
      },
      momentFormat: {
        stringify: (date) => {
          return date ? moment(date).format('MMMM YYYY') : ''
        },
        parse: (value) => {
          return value ? moment(value, 'MMMM YYYY').toDate() : null
        }
      },
      config: {
        title: 'Iuran BPJS Kesehatan',
        model_api: 'bpjs-kes-bills',
        getter: 'bpjs-kes-bills',
        setter: 'bpjs-kes-bills',
        pk_field: null,
        permission: {
          create: 'create-bpjs-kes-bills',
          read: 'view-bpjs-kes-bills',
          update: 'update-bpjs-kes-bills',
          delete: 'delete-bpjs-kes-bills'
        },
        slave: [
          {
            name: 'Rincian Iuran',
            permission: 'view-bpjs-kes-bill-details',
            module: 'bpjs/bpjs-kes-bill-details',
            as_param: 'bpjs_kes_bill_id',
            key_field: 'id'
          }
        ],
        fields: [
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', label: 'Dibuat', methods: { create: false, update: false, filter: false } },
          { id: 'updated_at', label: 'Diperbarui', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'description', label: 'Deskripsi', methods: { create: { type: 'textarea' }, update: { type: 'textarea' } } },
          { id: 'total_iuran_company_value', label: 'Total Iuran Perusahaan', methods: { create: false, update: false, list: { transform: 'money' }, detail: { transform: 'money' } } },
          { id: 'total_iuran_employee_value', label: 'Total Iuran Tenaga Kerja', methods: { create: false, update: false, list: { transform: 'money' }, detail: { transform: 'money' } } },
          { id: 'total_iuran_tambahan_value', label: 'Total Iuran Tambahan', methods: { create: false, update: false, list: { transform: 'money' }, detail: { transform: 'money' } } },
          { id: 'total_iuran_value', label: 'Total', methods: { create: false, update: false, list: { transform: 'money' }, detail: { transform: 'money' } } },
          { id: 'periode_month', label: 'Periode', methods: { create: false, update: false, list: { transform: 'period' }, detail: { transform: 'period' } } }
        ]
      }
    }
  },
  mounted () {
    this.getThisMonth()
  },
  methods: {
    redirect (e) {
      this.$router.push({ name: e, query: { view: 'list' } }).catch((err) => { return err })
    },
    checkThisMonth () {
      this.getPremiMonth()
      this.getPeriodMonth()
    },
    getThisMonth () {
      const formData = {
        periode_month: moment(new Date()).set('date', 1).format('YYYY-MM-DD')
      }
      this.$_api.list(this.config.setter, formData).then(res => {
        this.isAlertShow = !res.data.length
      }).catch((err) => {
        this.$_alert.error(err)
      })
    },
    getPremiMonth () {
      this.isLoadingPremi = true
      const formData = {
        periode_month: moment(moment(this.firstForm.periode_month, 'MMMM YYYY').toDate()).format('YYYY-MM-DD')
      }
      this.$_api.list('config-prosen-bpjs-kesehatan', formData).then(res => {
        this.isLoadingPremi = false
        this.isAvailablePremi = !!res.data.length
      }).catch((err) => {
        this.isLoadingPremi = false
        this.$_alert.error(err)
      })
    },
    getPeriodMonth () {
      this.isLoadingPeriod = true
      const formData = {
        periode_month: moment(moment(this.firstForm.periode_month, 'MMMM YYYY').toDate()).format('YYYY-MM-DD')
      }
      this.$_api.list('payroll-periodes', formData).then(res => {
        this.isLoadingPeriod = false
        this.isAvailablePeriod = !!res.data.length
      }).catch((err) => {
        this.isLoadingPeriod = false
        this.$_alert.error(err)
      })
    },
    generateMonth () {
      const formData = {
        periode_month: moment(moment(this.firstForm.periode_month, 'MMMM YYYY').toDate()).format('YYYY-MM-DD')
      }
      this.$_api.create('custom/bpjs-kes-bills', formData).then(res => {
        this.$children[0].getEvent('detail', res.data)
        this.isAlertShow = false
        this.$set(this.firstForm, 'periode_month', null)
        this.$_alert.success(null, this.config.title + ' berhasil dibuat')
      }).catch((err) => {
        this.$_alert.error(err)
      })
    }
  }
}
</script>



